<template>
    <div>
         <div class="info_dev">
			<div class="info_dev_text">
				<p>The work was performed by Yuri Batalin and Egor Mitrofanov</p>
				Variant 81327
			</div>
		</div>
        <div class="login_side">
            <form class="form_log" @submit.prevent="submitHandler" method="POST">
                <div id="info_log">
                    <a id="text">
                        Account Registration
                    </a>
                    <div class="input_par">
                        <input
                            class="input"
                            type="text"
                            name="name"
                            id="name"
                            placeholder="Name"
                            v-model.trim="userNameReg"
                            :class="{invalid: ($v.userNameReg.$dirty && !$v.userNameReg.required)}"
                        >
                        <div>
                            <small
                                class="errorText"
                                v-if="$v.userNameReg.$dirty && !$v.userNameReg.required">
                                    Поле не должно быть пустым
                            </small>
                        </div>
                    </div>
                    <div class="input_par">
                        <input 
                            class="input"
                            type="password"
                            name="pass"
                            id="pass"
                            placeholder="Password"
                            v-model.trim="passReg"
                            :class="{invalid: ($v.passReg.$dirty && !$v.passReg.required) || ($v.passReg.$dirty && !$v.passReg.minLength)}"
                        >
                        <div>
                            <small
                                class="errorText"
                                v-if="$v.passReg.$dirty && !$v.passReg.required">
                                Введите пароль
                            </small>
                            <small
                                class="errorText"
                                v-else-if="$v.passReg.$dirty && !$v.passReg.minLength">
                                Пароль должен иметь больше {{$v.passReg.$params.minLength.min}} символов
                            </small>
                        </div>
                    </div>
                    <div class="input_par">
                        <input 
                            class="input"
                            type="password"
                            name="pass2"
                            id="pass2"
                            placeholder="Repeat Password"
                            v-model.trim="$v.pass.$model"

                        >
                        <div>
                            <small
                                class="errorText"
                                v-if="$v.pass.$dirty && !$v.pass.sameAsPassword">
                                Пароли не совпадают
                            </small>
                        </div>
                    </div>
                    <div class="input_par">
                        <button class="btn_in" type="submit">
                            Registration
                        </button>
                    </div>
                    <div class="input_par">
                        <a>
                            Already have an account?
                        </a>
                        <router-link to="/login">Sing in</router-link>
                    </div>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import { required, sameAs, minLength } from 'vuelidate/lib/validators'
import axios from 'axios'

export default {
    name: 'reg',
    data:() => ({
        userNameReg: '',
        passReg: '',
        pass: ''
    }),
    validations:{
        userNameReg: {required},
        passReg: {required, minLength: minLength(5)},
        pass: {sameAsPassword: sameAs('passReg')}
    },
    methods: {
        submitHandler(){
            if (this.$v.$invalid){
                this.$v.$touch()
                return
            }
            const formDataReg = {
                userNameReg: this.userNameReg,
                passReg: this.passReg
            }
            axios.post('http://localhost:8080/rest/reg', formDataReg).then(function (response) {
               console.log(response);
            }).catch(function (error) {
                console.log(error);
            });
            this.$router.push('/login')
        }
    }
}
</script>